body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  height: 100vh;
  width: 100vw;
  background-color: rgb(237, 235, 235);
  
}

.header h1{
  text-align: center;
  padding-top: 3rem;
}
 p{
   text-align: center;
 }

.header {
  background-color: #010c3f;
  color:bisque;
  
  height: 250px;
}


.emph1 {
  text-align: center;
  margin-bottom: 2rem;
}
.card-section{
  display: flex;
  justify-content: center; 
  align-items: center;
  margin: 1rem 0;
  flex-wrap: wrap;
}
/* for modal div */

/* .hidden {
  display: none;
} */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(232, 232, 232,0.6);
  -webkit-backdrop-filter: blur(1px);
          backdrop-filter: blur(1px);
  z-index: 1;
}

.close-modal {
  position: absolute;
  top: 1rem;
  right: 6rem;
  font-size: 2rem;
  color: #333;
  cursor: pointer;
  border: none;
  background: none;
}

.modal-content {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 100%;
  background-color: rgba(232, 232, 232);
  padding: 20px;
  margin: 15% auto;
  border-radius: 5px;
  z-index: 2;
  color: var(--secondary-color);
  text-align: center;
}

.modal-content .card {
  text-align: center;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  padding: 1rem;
 
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  
  width: 40%;
  margin: 1rem;
  }
/* for effect */
 .emp_card {
  transition: 0.3s;
 }
 .emp_card:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
 }
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  background-color: #9da9da65;;
 
}

.container {
  text-align: center;
  color: #010c3f;
  font-weight:lighter;
  /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
  /* padding: 2px 16px; */
}

.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6.94rem;
  margin-bottom: 6.33rem;
}

.SearchBar input {
  width: 287px;
  padding: .88rem 0;
  border: 1px solid rgb(225, 223, 223);
  border-radius: 3px;
  margin-bottom: 2.22rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
}

.SearchBar input:focus {
  outline: none;
}

.SearchButton {
  cursor: pointer;
  width: 8.11rem;
  padding: .77rem 0;
  border-radius: 54px;
  background-color: #010c3f;
  text-align: center;
  font-size: .833rem;
  transition: background-color .25s;
  border: 0px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 1rem;
}

.SearchButton:hover {
  background-color: rgba(100, 93, 121, 0.7);
}

/* for small screen */
@media only screen and (max-width:768px) {
   /* for card  */
   .card-section {
    flex-direction: column;
      }
.card {
    width: 100%;
    margin: 1rem 0;
}
.close-modal {
 
  top: 0.2rem;
  right: 2rem;

}

}

